import { action, computed, makeObservable, observable } from "mobx";
import { CourseModel, CourseStatus } from "../../models/CourseModel";
import { TermModel } from "../../models/TermModel";
import { CourseState } from "../../states/CourseState";

export interface ICourseGroup {
  id: string,
  title: string,
  term: TermModel | null,
  courses: CourseModel[],
}

export interface IYearGroup {
  id: string,
  year: number
  title: string,
  courseGroups: ICourseGroup[],
}

export interface IPlannerState {
  id: string,
  title: string,
  allocatedGroups: ICourseGroup[],
  yearGroups: IYearGroup[],
  completedGroup: ICourseGroup,
  plannedGroup: ICourseGroup,
}

export interface ICurrentDraggableProps {
  isDragging: boolean,
  possibleTerms: TermModel[]
  prerequisiteCourseIds: Set<string>
}

type Transaction_t =
  ["-", string, number, number, CourseModel[]]
  | ["+", string, number, CourseModel[]];

type PreviousTransaction = {
  selected: CourseModel[],
  transactions: Transaction_t[],
}

export class PlannerViewModel {
  constructor(courseState: CourseState) {
    makeObservable<PlannerViewModel,
      "_currentDraggableProps"
    >(this, {
      _currentDraggableProps: observable,
      currentDraggableProps: computed,
      clearCurrentDraggableProps: action,
    })

    this._courseState = courseState
    this._plannerState = {
      id: "plannerState",
      title: "Visualizer",
      allocatedGroups: [],
      yearGroups: [],
      completedGroup: {
        id: "completed",
        title: "Completed",
        term: null,
        courses: [],
      },
      plannedGroup: {
        id: "planned",
        title: "Planned",
        term: null,
        courses: [],
      },
    }

    makeObservable<PlannerViewModel,
      "_selectedGroup"
    >(this, {
      _selectedGroup: observable,
      selectedGroup: computed,
    });

    makeObservable<PlannerViewModel,
      "_draggingCourseId"
    >(this, {
      _draggingCourseId: observable,
      draggingCourseId: computed,
    });
  }

  private _courseState: CourseState
  get courseState(): CourseState {
    return this._courseState
  }

  private _plannerState: IPlannerState
  get plannerState(): IPlannerState {
    this.synchronizePlannerState()
    return this._plannerState
  }

  private _MAX_BUFFER = 5;
  private _transactionBuffer: PreviousTransaction[] = [];
  private _currentTransaction: Transaction_t[] = [];

  //#region selectedGroup

  private _selectedGroup: ICourseGroup = {
    id: "selected",
    title: "Selected",
    term: null,
    courses: [],
  };
  get selectedGroup(): ICourseGroup {
    return this._selectedGroup;
  }
  set selectedGroup(state: ICourseGroup) {
    this._selectedGroup = state;
  }

  //#endregion

  startTransaction() {
    this._currentTransaction = [];
  }

  undoTransaction() {
    const lastTransaction = this._transactionBuffer.shift();
    if (!lastTransaction) return;

    lastTransaction.transactions.forEach((transaction: Transaction_t) => {
      if (transaction[0] === "+") {
        this.getCourseGroupById(transaction[1])?.courses
          .splice(transaction[2], transaction[3].length);
      } else if (transaction[0] === "-") {
        transaction[4].forEach((course: CourseModel) => {
          if (transaction[1] === "trash") course.setUnplanned();
          else if (transaction[1] === "planned") course.setPlanned();
          else if (transaction[1] === "completed") course.setCompleted();
          else course.setAllocated(TermModel.fromMappingName(transaction[1]));
        })
        this.getCourseGroupById(transaction[1])?.courses
          .splice(transaction[2], transaction[3], ...transaction[4]);
      }
    });

    this._selectedGroup = {
      ...this._selectedGroup,
      courses: lastTransaction.selected,
    };

    this.synchronizePlannerState();
  }

  finishTransaction() {
    if (this._transactionBuffer.length >= this._MAX_BUFFER) this._transactionBuffer.shift();
    this._transactionBuffer.unshift({
      selected: this._selectedGroup.courses.slice(),
      transactions: this._currentTransaction.slice()
    });
    this._currentTransaction = [];
  }

  setCourseByDroppableId(course: CourseModel, sourceDroppableId: string, destDroppableId: string, draggableItemMap: Map<CourseModel, HTMLDivElement>) {
    if (sourceDroppableId === destDroppableId) return;

    if (destDroppableId === "trash") {
      course.setUnplanned()
      draggableItemMap.delete(course);
    } else if (destDroppableId === "completed") {
      course.setCompleted()
    } else if (destDroppableId === "planned") {
      course.setPlanned()
    } else {
      course.setAllocated(TermModel.fromMappingName(destDroppableId))
    }
  }

  unselectAllCourses() {
    this.selectedGroup = {
      ...this.selectedGroup,
      courses: [],
    };
  }

  selectCourses(courses: CourseModel[]) {
    this.selectedGroup = {
      ...this.selectedGroup,
      courses: courses,
    };
  }

  moveSelectedToRemoved(draggableItemMap: Map<CourseModel, HTMLDivElement>) {
    this.selectedGroup.courses.filter((courseModel: CourseModel) => {
      return courseModel.courseStatus !== CourseStatus.UNPLANNED;
    }).forEach((courseModel: CourseModel) => {
      courseModel.setUnplanned();
      draggableItemMap.delete(courseModel);
    });
    this.unselectAllCourses();
  }

  moveCourseToRemoved(draggableItemMap: Map<CourseModel, HTMLDivElement>, course?: CourseModel) {
    if (!course) {
      this.moveSelectedToRemoved(draggableItemMap);
      return;
    }

    if (course.courseStatus !== CourseStatus.UNPLANNED) {
      course.setUnplanned();
      draggableItemMap.delete(course);
    }

    if (this.selectedGroup.courses.includes(course)) {
      this.moveSelectedToRemoved(draggableItemMap);
    }

    this.synchronizePlannerState();
  }

  moveSelectedToPlanned() {
    this.selectedGroup.courses.filter((courseModel: CourseModel) =>
      courseModel.courseStatus !== CourseStatus.PLANNED
    ).forEach(courseModel => courseModel.setPlanned());
  }

  moveCourseToPlanned(course?: CourseModel) {
    if (!course) {
      this.moveSelectedToPlanned();
      return;
    }

    if (course.courseStatus !== CourseStatus.PLANNED) {
      course.setPlanned();
    }

    if (this.selectedGroup.courses.includes(course)) {
      this.moveSelectedToPlanned();
    }

    this.synchronizePlannerState();
  }

  moveSelectedToCompleted() {
    this.selectedGroup.courses.filter((courseModel: CourseModel) =>
      courseModel.courseStatus !== CourseStatus.COMPLETED
    ).forEach(courseModel => courseModel.setCompleted());
  }

  moveCourseToCompleted(course?: CourseModel) {
    if (!course) {
      this.moveSelectedToCompleted();
      return;
    }

    if (course.courseStatus !== CourseStatus.COMPLETED) {
      course.setCompleted();
    }

    if (this.selectedGroup.courses.includes(course)) {
      this.moveSelectedToCompleted();
    }

    this.synchronizePlannerState();
  }

  //#region draggingCourseId

  private _draggingCourseId: undefined | string = undefined;
  get draggingCourseId(): undefined | string {
    return this._draggingCourseId;
  }
  set draggingCourseId(id: undefined | string) {
    this._draggingCourseId = id;
  }

  //#endregion

  private synchronizeCourseList(referenceCourses: CourseModel[], targetCourses: CourseModel[]): void {
    let expectedCourses = new Set(referenceCourses)
    let actualCourses = new Set(targetCourses)

    let coursesToRemove = new Set([...actualCourses].filter(course => !expectedCourses.has(course)))
    let coursesToAdd = new Set([...expectedCourses].filter(course => !actualCourses.has(course)))

    coursesToRemove.forEach(course => {
      let index = targetCourses.indexOf(course)
      targetCourses.splice(index, 1)
    })

    coursesToAdd.forEach(course => {
      targetCourses.push(course)
    })
  }

  calculateDisplayableTerms(): TermModel[] {
    let maxAllocatedYear = this._courseState.getMaxAllocatedYear
    const currentYear = new Date().getFullYear()
    let displayableTerms: TermModel[] = []
    for (let year: number = currentYear; year <= maxAllocatedYear + 1; ++year) {
      displayableTerms.push(new TermModel(year, "Summer Term"))
      displayableTerms.push(new TermModel(year, "Term 1"))
      displayableTerms.push(new TermModel(year, "Term 2"))
      displayableTerms.push(new TermModel(year, "Term 3"))
    }
    return displayableTerms
  }

  synchronizePlannerState(): void {
    const displayableTerms = this.calculateDisplayableTerms()
    const uniqueYears = [...new Set(displayableTerms.map(term => term.year))].sort()
    const allocatedGroups: ICourseGroup[] = displayableTerms.map((term: TermModel) => {
      return {
        id: term.getMappingName,
        title: term.getFullName,
        term: term,
        courses: this._plannerState.allocatedGroups.find(group => group.id === term.getMappingName)?.courses ?? [],
      }
    })
    let newPlannerState: IPlannerState = {
      ...this._plannerState,
      allocatedGroups: allocatedGroups,
      yearGroups: uniqueYears.map((year: number) => {
        return {
          id: `yearGroup_${year.toString()}`,
          title: year.toString(),
          year: year,
          courseGroups: allocatedGroups.filter(group => group.term?.year === year) ?? [],
        }
      }),
    }

    newPlannerState.allocatedGroups.forEach((courseGroup: ICourseGroup) => {
      this.synchronizeCourseList(this._courseState.termCourseMap.get(courseGroup.id) ?? [], courseGroup.courses)
    })

    newPlannerState.yearGroups.forEach(yearGroup => {
      yearGroup.courseGroups.forEach((courseGroup: ICourseGroup) => {
        this.synchronizeCourseList(this._courseState.termCourseMap.get(courseGroup.id) ?? [], courseGroup.courses)
      })
    })

    this.synchronizeCourseList(this._courseState.plannedCourses, newPlannerState.plannedGroup.courses)
    this.synchronizeCourseList(this._courseState.completedCourses, newPlannerState.completedGroup.courses)
    this._plannerState = newPlannerState
  }

  private getCourseGroupById(groupId: string): ICourseGroup | undefined {
    if (this._plannerState.completedGroup.id === groupId) {
      return this._plannerState.completedGroup
    } else if (this._plannerState.plannedGroup.id === groupId) {
      return this._plannerState.plannedGroup
    } else {
      return this._plannerState.allocatedGroups.find(group => group.id === groupId)
    }
  }

  getAllGroups(): ICourseGroup[] {
    return [this._plannerState.completedGroup, this._plannerState.plannedGroup, ...this._plannerState.allocatedGroups];
  }

  spliceByGroupId(droppableId: string, movedCourses: CourseModel[], start: number, deleteCount?: number): CourseModel[];

  spliceByGroupId(droppableId: string, movedCourses: CourseModel[], start: number, deleteCount: number, ...items: CourseModel[]): CourseModel[];

  spliceByGroupId(droppableId: string, movedCourses: CourseModel[], start: number, deleteCount?: number, ...items: CourseModel[]): CourseModel[] {
    const group = this.getCourseGroupById(droppableId);
    if (!group) return [];

    if (deleteCount) {
      this._currentTransaction.push(["-", droppableId, start, deleteCount, [...movedCourses]]);
      return group.courses.splice(start, deleteCount, ...items)
    } else {
      this._currentTransaction.push(["+", droppableId, start, [...items]]);
      return group.courses.splice(start, 0, ...items);
    }
  }

  //#region currentDraggableProps

  private _currentDraggableProps: ICurrentDraggableProps = {
    isDragging: false,
    possibleTerms: [],
    prerequisiteCourseIds: new Set<string>(),
  }

  get currentDraggableProps(): ICurrentDraggableProps {
    return { ...this._currentDraggableProps }
  }

  set currentDraggableProps(props: ICurrentDraggableProps) {
    this._currentDraggableProps = props;
  }

  clearCurrentDraggableProps(): void {
    this.currentDraggableProps = {
      isDragging: false,
      possibleTerms: [],
      prerequisiteCourseIds: new Set<string>(),
    }
  }

  //#endregion

}
